export default {
  description: 'Test if two or more values are equal',
  usage: ['value.eq(value[, value, ...]) → bool'],
  example: {
    description: 'Check if 2 values are equal',
    code: `v.expr({ value: 1 }).eq({ value: 1 })`
  }
}




